<template>
    <div :style="'background:url('+bg+') no-repeat;background-size:cover;height:675px;'">
        <h1 class="main-title">{{title}}</h1>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input v-model="userName" size="large" placeholder="请输入用户名"></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Input type="password" v-model="password" size="large" placeholder="请输入密码"></Input>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Button @click="login" long type="success" >登录</Button>
            </Col>
        </Row>
        <Row class="write">
            <Col :xs="{ span: 20, offset:2  }" :lg="{ span: 8, offset:8 }">
            <Button @click="register" long type="primary" >注册</Button>
            </Col>
        </Row>
    </div>
</template>

<script>
    import requestObj from '../../api/api';
    export default {
        name: "login",
        data(){
            return {
	            bg:require('../../assets/frontend/c-bg.jpg'),
                userName:'',
                password:'',
            };
        },
        computed:{
            title(){
                if(this.$route.query.title){
                	return this.$route.query.title;
                }else{
                	return '登录'
                }
            },
        },
        methods:{
            login(){
                var self=this;
                requestObj({
                    url:'login.do',
                    method:'GET',
                    params:{
                        username:self.userName,
                        password:self.password,
                    },
                    success:function (res){
                        localStorage.setItem("userID",res.data.data.id);
                        self.$router.go(-1);
                    },
                    fail:function (err){
                        console.log(err);
                    },
                });
            },
            register(){
                this.$router.push('/register');
            },
        },
    }
</script>

<style scoped>

</style>


